





























































    export default {
        data() {
            return {
				order_noSearch:'',
				customer_nameSearch:'',
                uid: 0,
                list: [],
                clientId: 0,
                clientName: "",
                showClient: false,
                clientList: [],
                showGood: false,
                goodList: [],
                textGood: "",
                itemIndex: 0,
                preventEvent: false,
				page:1,
				pageShop:1,
				pageexpress:1,
				total:'',
				totalShop:'',
				username:'',
				
				date: "",//日期
				order_no:'',//单号
				checkedArr:[], //复选框选中的值，工种
				allCheck : {
					name : '全选',
					value : 'all',
					checked : false	
				},
				invoiceList: [
					{
						id:0,
						name:'否'
					},
					{
						id:1,
						name:'是'
					},
				],
				showExpress:false,
				ExpressList:[],
				expressSearch:'',
				totalexpress:'',
				salesId:'',
            }
        },
        mounted() {
        },
        onLoad(option) {
        	this.uid = this.common.get("uid")
        	this.salesId = this.option = JSON.parse(decodeURIComponent(option.data))
        	setTimeout(() =>{
        		// 进入页面
        		this.http({
        		    url: this.api.Change_detail,
        		    method: "POST",
        		    data: {
        		        uid: this.uid,
        				id:this.salesId
        		    },
        		    success: res => {
        				this.clientName = res.sales_order_no
        				this.order_no = res.order_no
        				this.date = res.date,//日期
        				this.order_no = res.order_no,//单号
        				res.total_data.map((item,index) =>{
        					this.list.push({
        						'goods_name':item.goods_name,
								'count':item.count,
								'remark':item.remark,
								'sales_count':item.sales_count,
        					})
        				})
        		    }
        		})
        	},500)
        },
        methods: {}
    }
